* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 16px 24px;
  font-family: Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

input {
  font: inherit;
  padding: 8px;
}

button {
  cursor: pointer;
  padding: 8px 12px;
}

span {
  padding-left: 8px;
}

.box {
  width: 280px;
  height: 140px;
  border: 1px solid teal;
  padding: 15px;
  display: none;
}

.clock {
  width: 550px;
  height: 550px;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("https://i.ibb.co/dBGxSK1/hiclipart-com-3.png");
  background-size: cover;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.clock-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  transform: translateX(-50%);
}

.clock-center {
  width: 20px;
  height: 20px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 268px;
  left: 268px;
}

.clock-seconds__arrow {
  width: 5px;
  height: 40%;
  bottom: 50%;
  left: 50%;
}

.clock-minutes__arrow {
  width: 7px;
  height: 33%;
  bottom: 50%;
  left: 50%;
}

.clock-hours__arrow {
  width: 12px;
  height: 24%;
  bottom: 50%;
  left: 50%;
}

.date {
  width: 100px;
  height: 100px;
  color: #000;
}

.calendar {
  justify-content: space-between;
  font-size: 14px;
  font-weight: 400;
  display: flex;
}

.date-style {
  text-shadow: 3px 3px 2px #cecbcd;
  justify-content: space-between;
  font-size: 30px;
  font-weight: 600;
  display: flex;
}

.digital-clock {
  text-align: center;
  text-shadow: 3px 3px 2px #cecbcd;
  font-size: 60px;
}

.js-game__container {
  width: 153px;
  background-color: #cecbcd;
  border: 1px solid #000;
  margin-top: 8px;
  display: flex;
}

.game__item {
  width: 50px;
  height: 50px;
  text-align: center;
  vertical-align: middle;
  border: 1px solid #000;
  outline: auto;
  line-height: 50px;
}

